<template>
    <div class="money">
        <a-breadcrumb class="bread">
            <a-breadcrumb-item>
                <a href="/customerService/pc">{{ $t("language.service") }}</a>
            </a-breadcrumb-item>
            <a-breadcrumb-item class="text">
                {{ $t("language.arrived") }}
            </a-breadcrumb-item>
        </a-breadcrumb>
        <div class="content">
            <div class="tabs">
                <div
                    class="item"
                    :class="{ Selected: tabIndex == 1 }"
                    @click="onChangeTabs(1)"
                >
                    {{ $t("language.arrived") }}
                </div>
                <div
                    class="item"
                    :class="{ Selected: tabIndex == 2 }"
                    @click="onChangeTabs(2)"
                >
                    {{ $t("language.record") }}
                </div>
            </div>
            <div class="card">
                <template v-if="tabIndex == 1">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.memberaccount") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.name"
                                    :placeholder="$t('language.account')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.amount1") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.money"
                                    type="number"
                                    :placeholder="$t('language.amount')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.withdrawaltime") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-date-picker
                                    v-model="formState.time"
                                    :disabledDate="disabledDate"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.verification") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <div class="flexCode">
                                    <a-input
                                        v-model="formState.code"
                                        type="number"
                                        :placeholder="
                                            $t('language.verification1')
                                        "
                                    />
                                    <div>
                                        <img
                                            :src="captchaImg"
                                            class="codeImg"
                                            @click="getCaptcha"
                                        />
                                    </div>
                                </div>
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <div>
                                    <p class="tips">
                                        {{ $t("language.tipText") }}
                                    </p>
                                    <p class="tips">
                                        {{ $t("language.text6") }}
                                    </p>
                                </div>
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="onSubmit"
                                >
                                    {{ $t("language.submitnow") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="tabIndex == 2">
                    <div>
                        <a-table
                            :columns="columns"
                            :data-source="data"
                            :rowKey="
                                (record, index) => {
                                    return index;
                                }
                            "
                            bordered
                            :pagination="{ pageSize: 5 }"
                        >
                            <span slot="applyWithdrawalTime" slot-scope="text">
                                <span>
                                    {{ $moment(text).format("YYYY-MM-DD") }}
                                </span>
                            </span>
                            <span slot="status" slot-scope="text">
                                <span v-if="text == 0">
                                    {{ $t("language.unprocessed") }}</span
                                >
                                <span v-if="text == 1">
                                    {{ $t("language.success") }}</span
                                >
                                <span v-if="text == 2">
                                    {{ $t("language.failure") }}</span
                                >
                                <span v-if="text == 3" class="processing">
                                    {{ $t("language.Processing") }}
                                </span>
                            </span>
                        </a-table>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import { Modal } from "ant-design-vue";
export default {
    data() {
        return {
            formState: {},
            tabIndex: 1,
            columns: [],
            data: [],
            captchaImg: "",
            captchaKey: "",
            subimgBtn: true,
        };
    },
    created() {
        this.formState.name = this.$config.username;
        this.getCaptcha();
        this.columns = [
            {
                key: "time",
                title: this.$t("language.application1"),
                align: "center",
                dataIndex: "applyWithdrawalTime",
                scopedSlots: {
                    customRender: "applyWithdrawalTime",
                },
            },
            {
                title: this.$t("language.actualname"),
                align: "center",
                dataIndex: "memberName",
            },
            {
                title: this.$t("language.Amount"),
                align: "center",
                dataIndex: "withdrawalAmount",
            },
            {
                key: "type",
                title: this.$t("language.status"),
                align: "center",
                dataIndex: "status",
                scopedSlots: {
                    customRender: "status",
                },
            },
            {
                title: this.$t("language.remarks"),
                align: "center",
                dataIndex: "remarks",
                ellipsis: true,
            },
        ];
    },
    methods: {
        disabledDate(current) {
            return current > this.$moment().format();
        },
        onChangeTabs(i) {
            if (i == 2 && !this.$config.username) {
                Modal.error({
                    title: this.$t("language.tips"),
                    content: this.$t("language.login"),
                });
                return;
            }
            if (i == 2) {
                this.getPayment();
            }
            this.tabIndex = i;
            this.formState = {};
            this.formState.name = this.$config.username;
        },
        getCaptcha() {
            this.$http.get(this.$api.getCaptcha).then((res) => {
                console.log("图片", res);
                if (res.code == 0) {
                    this.captchaKey = res.data.captchaKey;
                    this.captchaImg = res.data.captcha_image_content;
                }
            });
        },
        //获取申请列表
        getPayment() {
            let parames = {
                memberName: this.$config.username,
            };
            this.$http.post(this.$api.getPayment, parames).then((result) => {
                console.log(result);
                if (result.code == 0) {
                    this.data = result.data;
                }
            });
        },
        onSubmit() {
            if (!this.subimgBtn) {
                return;
            }
            if (!this.formState.name) {
                this.$message.warning(this.$t("language.account"));
                return;
            }
            if (!this.formState.money) {
                this.$message.warning(this.$t("language.amount"));
                return;
            }
            if (!this.formState.time) {
                this.$message.warning(this.$t("language.timegetmoney"));
                return;
            }
            if (!this.formState.code) {
                this.$message.warning(this.$t("language.verification1"));
                return;
            }
            var currentPicker = this.$moment(this.formState.time).format(
                "YYYY-MM-DD"
            );
            let parames = {
                memberName: this.formState.name,
                applyWithdrawalTime: currentPicker,
                withdrawalAmount: this.formState.money,
                captchaCode: this.formState.code,
                captchaKey: this.captchaKey,
            };
            if (this.$config.clientItem == "aygj") {
                if (!this.$config.username) {
                    parames.memberName = parames.memberName + "ay";
                } else {
                    var disLength = parames.memberName.length;
                    var shortName = parames.memberName.substring(
                        disLength - 2,
                        disLength
                    );
                    if (shortName != "ay") {
                        parames.memberName = parames.memberName + "ay";
                    }
                }
            }
            console.log(parames);
            this.subimgBtn = !this.subimgBtn;
            this.$http.post(this.$api.addPayment, parames).then((res) => {
                this.subimgBtn = !this.subimgBtn;
                this.getCaptcha();
                if (res.code == 0) {
                    console.log(res);
                    this.showModel();
                    // this.$message.success(this.$t("language.successfully"));
                    // this.$router.go(-1);
                }
            });
        },
        showModel() {
            let _this = this;
            const model = Modal.info({
                title: "提示",
                content: this.$t("language.tipssueecc"),
                centered: true,
                okText: this.$t("language.seePage"),
                icon: "none",
                class: "modelSueecc",
                closable: true,
                maskClosable: true,
                onOk() {
                    return new Promise((resolve, reject) => {
                        _this.onChangeTabs(2);
                        resolve();
                    });
                },
                onCancel() {
                    _this.$router.go(-1);
                    console.log("关闭----------");
                },
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.money {
    margin: 10px 18%;
    .content {
        margin: 20px 15%;
        .tabs {
            display: flex;
            .item {
                background: #fff;
                color: #bcbcbc;
                font-size: 16px;
                padding: 16px 27px;
                cursor: pointer;
                border-bottom: 1px solid #e4e4e4;
            }
            .Selected {
                color: var(--themeColor);
                border-bottom: 2px solid var(--themeColor);
            }
        }
        .card {
            background: #fff;
            padding: 30px;
            .rowItem {
                margin-bottom: 20px;
                .btnCol {
                    text-align: left;
                    .btnNext {
                        background: var(--themeColor);
                        border: none;
                        padding-left: 35px;
                        padding-right: 35px;
                    }
                }
                .flexBox {
                    display: flex;
                    .btnCode {
                        margin-left: 10px;
                        background: var(--themeColor);
                        border: none;
                    }
                }
                .lable {
                    margin: 0;
                    text-align: right;
                    line-height: 44px;
                    padding-right: 10px;
                    color: #333;
                    font-size: 16px;
                }
                .flexCode {
                    display: flex;
                    .codeImg {
                        height: 44px;
                        margin-left: 10px;
                    }
                }
                .tips {
                    color: var(--themeColor);
                }
            }
        }
    }
    .ant-calendar-picker {
        width: 100%;
    }
    .processing {
        color: #f9a425;
    }
}
</style>